article.post-card-container {
  max-width: 30rem;
  max-width: 70rem;
  box-shadow: 0 0.3rem 0.5rem rgb(0, 0, 0, 0.3);
  border-radius: 0.5rem;
  transition: transform var(--normal-transition) ease-in-out;
  cursor: pointer;
}

article.post-card-container:hover {
  transform: scale(1.01);
}

img.post-img {
  width: 100%;
  height: 15rem;
  object-fit: cover;
}

div.post-card-details {
  padding: 0 1rem;
  margin-top: 1rem;
}

h3.post-name {
  font-size: clamp(var(--m-small-font-title), 1.8vw, var(--d-small-font-title));
  line-height: clamp(
    var(--small-line-height),
    2.4vw,
    var(--ex-big-line-height)
  );
}

p.post-content {
  font-size: clamp(var(--m-text-size), 1.6vw, var(--d-small-text-size));
  margin-top: 1rem;
  line-height: clamp(
    var(--ex-small-line-height),
    1.95vw,
    var(--big-line-height)
  );
}

footer.post-card-author-info {
  margin-top: 1rem;
  padding: 1rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-top: 0.1rem solid #3332;
}

footer.post-card-author-info div {
  display: flex;
  align-items: center;
}

img.post-card-author {
  width: 3rem;
  height: 3rem;
}

h4.past-card-author-name {
  margin-left: 0.3rem;
  font-size: 1.3rem;
  line-height: clamp(
    var(--ex-small-line-height),
    1.95vw,
    var(--big-line-height)
  );
}

time.post-date {
  font-size: 1.3rem;
  line-height: clamp(
    var(--ex-small-line-height),
    1.95vw,
    var(--big-line-height)
  );
}
